import React from "react";
import { Grid } from "@material-ui/core";
import StackGrid from "react-stack-grid";
import { useState, useEffect } from "react";

//import LinkCard from "../components/LinkCards";
import firebase from "../firebase"
import SimpleSnackbar from "../components/Snackbar";

import { makeStyles } from "@material-ui/core/styles"
import { Card } from "@material-ui/core"
import { CardActionArea } from "@material-ui/core"
import { CardActions } from "@material-ui/core"
import { CardContent } from "@material-ui/core"
import { CardMedia } from "@material-ui/core"
import { Typography } from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import EditIcon from "@material-ui/icons/Edit"
import IconButton from "@material-ui/core/IconButton"

const useStyles = makeStyles((theme) => ({
	cardContainer: {
		background: "#c0c0c0",
		width: "100%"
	},
	cardContent: {
		lineHeight: 1.25
	}
}))

function Home() {

	const [openSnack, setOpenSnack] = useState(false);
	const linkData = useDetails()
	const links = linkData.map((link) => (
		<Grid item key={link.link_id}>
			<LinkCard 
				content={link.link_id}
				title={link.link_title}
				url={link.link_url}
				img={link.link_img}
			/>
		</Grid>
	))
	
	const size = useWindowSize()
	const cardWidth = () => {
		if (size.width < 321) return "100%";
		else if (size.width < 426) return "50%";
		else if (size.width < 769) return "33.3333333%";
		else if (size.width < 1025) return "25%";
		else return "20%";
	}
    
	return (
		<>
			<StackGrid 
				monitorImagesLoaded={true}
				columnWidth = {cardWidth()}
			>
				{links}
			</StackGrid>
			<SimpleSnackbar 
				message="Delete link?"
				show={openSnack}
			/>
		</>
	);

	function LinkCard(props) {

		const classes = useStyles()
		let hostName = getHostName(props.url)
	
		return (
			<>
				<Card className={classes.cardContainer} >
					<CardActionArea>
						{props.img !== "" 
							? 
								<CardMedia
									component="img"
									alt="Image"
									image={props.img}
									title="cat"
								/> 
							: ""}
						<CardContent>
							<Typography variant="overline">{hostName}</Typography>
							<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>{props.title}</Typography>
							<Typography className={classes.cardContent} variant="caption" gutterBottom>{props.url}</Typography>
						</CardContent>
					</CardActionArea>
					<CardActions>
						<IconButton size="small" color="primary">
							<EditIcon />
						</IconButton>
						<IconButton size="small" color="primary"
							onClick={(e) => deleteButton(props.content)}>
							<DeleteIcon />
						</IconButton>
					</CardActions>
				</Card>
			</>
		)
	}
	
	function deleteButton(a) {
		console.log(a)
		setOpenSnack(true)
	}
	
	function getHostName(url) {
		const matches = url.match(/^https?:\/\/(www.)?([^/?#]+)(?:[/?#]|$)/i)
		return matches && matches[2]
	}
}

// Hook
function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
        width: undefined,
        height: undefined,
    });

    useEffect(() => {
        // Handler to call on window resize
        function handleResize() {
        // Set window width/height to state
            setWindowSize({
                width: window.innerWidth,
                height: window.innerHeight,
            });
        }

        // Add event listener
        window.addEventListener("resize", handleResize);

        // Call handler right away so state gets updated with initial window size
        handleResize();

        // Remove event listener on cleanup
        return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount

    return windowSize;
}

function useDetails() {
	const [linkDetails, setLinkDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("links")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					link_id: doc.id,
					...doc.data()
				}))
                setLinkDetails(newDetails)
            })
        
        return () => unsubscribe()
    }, [])
    
    return linkDetails
}

export default Home;