import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import MenuItem from '@material-ui/core/MenuItem';
import { useState } from "react"

import collectionData from "../data/collectionData.js"
import firebase from "../firebase"

const useStyles = makeStyles((theme) => ({
  fabAdd: {
		position: "fixed",
		bottom: theme.spacing(8),
		right: theme.spacing(2),
		zIndex: 1000
  },
  formHolder: {
    position: "fixed",
    width: "100%",
    bottom: 0,
    padding: `${theme.spacing(3)}px ${theme.spacing(2)}px`,
    borderRadius: `${theme.spacing(1)}px ${theme.spacing(1)}px 0 0`,
    background: "#999",
    zIndex: 5,
    transition: "transform 0.3s ease-in-out"
  },
  formShown: {
      transform: "translate3d(0,0,0)"
  },
  formHidden: {
      transform: "translate3d(0,100%,0)"
  },
  formInput: {
      width: "100%",
      margin: `${theme.spacing(1)}px 0`
  },
  formTitle: {
      display: "flex",
      alignItems: "center",
      textTransform: "uppercase",
      paddingBottom: theme.spacing(1)
  },
  buttonHolder: {
      margin: `${theme.spacing(1)}px 0`
  },
  formButton: {
      marginRight: `${theme.spacing(1)}px`
  }
}))

export default function FormDialog(props) {

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const [folder, setFolder] = useState('0');
  const [title, setTitle] = useState("");
  const [url, setUrl] = useState("");

  const handleFolderSelect = (e) => {
    setFolder(e.target.value)
  }

  const handleTitleInput = (e) => {
    setTitle(e.target.value)
  }

  const handleUrlInput = (e) => {
    setUrl(e.target.value)
  }

  const handleFormSubmit = (e) => {
    e.preventDefault()
    firebase.firestore()
      .collection("links")
      .add({
        link_title: title,
        link_url: url,
        folder_id: folder,
        link_img: ""
      })
    setOpen(false)
  }

  return (
    <div>
      <Fab color="primary" aria-label="add"
        className={classes.fabAdd}
        onClick={handleClickOpen}
      >
        <AddIcon />
      </Fab>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <form onSubmit={handleFormSubmit} >
          <DialogTitle id="form-dialog-title">
            Add new {props.mode === "collection" ? "collection" : "link"}
          </DialogTitle>
          <DialogContent>
            <TextField
              multiline
              className={classes.formInput}
              id="link-title"
              label="Link Title"
              variant="outlined"
              value={title}
              onChange={handleTitleInput}
              size="small"
            />
            <TextField
              required
              type="url"
              className={classes.formInput}
              id="link-url"
              label="Link URL"
              variant="outlined"
              size="small"
              value={url}
              onChange={handleUrlInput}
              helperText="URL must start with http:// or https://"
            />
            <TextField
              className={classes.formInput}
              id="outlined-select-currency"
              select
              label="Collection"
              value={folder}
              onChange={handleFolderSelect}
              variant="outlined"
              size="small"
            >
              {collectionData.map((option) => (
                  <MenuItem 
                    key={option.folder_id} 
                    value={option.folder_id}
                  >
                    {option.folder_name}
                  </MenuItem>
              ))}
            </TextField>
          </DialogContent>
          <DialogActions>
            <Button type="submit" color="primary">
              Save
            </Button>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}